import React, { useState, useEffect } from 'react'
import {
    Grid, Card, Button,
    Dialog,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Box,
    DialogActions,
    TextField,
    FormControl,
    InputLabel,
    Select,
    MenuItem,

    Modal,
    Typography,
} from '@mui/material'
import { styled, useTheme } from '@mui/system'
import moment from 'moment'
import useStateRef from 'react-usestateref'

const CancelButton = styled(Button)(() => ({
    display: 'block',
    color: '#fff',
    // marginTop: '20px',
    width: '86px',
    backgroundColor: '#e15260',
    '&:hover': {
        backgroundColor: '#cc2333',
        color: '#fff'
    }
}))

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    // width: '100%',
    bgcolor: 'background.paper',
    border: '2px solid #fff',
    boxShadow: 24,
    p: 4,
    borderRadius: '15px'
}
const NotesModal = (props) => {
    const [clientId, setClientId] = useState('')
    const [notes, setNotes, notesRef] = useStateRef([])
    const [select, setSelect] = useState('')
    const [sessionData, setSessionData] = useState([])
    const [sessionId, setSessionId] = useState('')

    const closeModal = () => {
        setSelect('')
        setNotes([])
        setSessionData([])
        props.handleClose();
    }
    function formatNotes(data) {
        let notes = []
        data && data.map((item) => {
            notes.push(item.attributes)
        })
        return notes
    }

    function handleSession(e) {
        // setSessionId(data)
        setSelect(e.target.value)
        const result = sessionData.filter(item => item.attributes.session_id == e.target.value);
        if (result) {
            result[0].attributes.notes.data.sort(function (a, b) {
                return new Date(b.updatedAt) - new Date(a.updatedAt)
            })
            setNotes(formatNotes(result[0].attributes.notes.data))
        }
        console.log(notesRef.current)
        // setNotes(result[0].attributes.notes && )
    }

    useEffect(() => {

        if (props.data) {
            setClientId(props.data.clientId)
            setSessionId(props.data.sessionId)
            props.multipleSession && props.data.notes && setSessionData(props.data.notes)
            if (!props.multipleSession) {
                props.data.notes && props.data.notes.sort(function (a, b) {
                    return new Date(b.updatedAt) - new Date(a.updatedAt)
                })
                setNotes(props.data.notes)
            }
        }
    }, [props])

    const handleMaxWidthChange = (event) => {
        console.log('fadfs')
    };
    return (
        <React.Fragment>

            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.open}
                onClose={closeModal}
            >
                <DialogTitle>Notes</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Client : {clientId}
                    </DialogContentText>
                    {props.multipleSession && (
                        <Box
                            noValidate
                            component="form"
                            lg={12} md={6} sm={12} xs={12}
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'center',
                                //   alignItems: 'flex-start',
                                //   m: 'auto',
                                width: '50%',
                            }}
                        >
                            <FormControl sx={{ mt: 2, minWidth: 120 }}>
                                <InputLabel htmlFor="max-width">Select Session</InputLabel>
                                <Select
                                    autoFocus
                                    value={select}
                                    onChange={handleSession}
                                    label="Select Session"
                                    inputProps={{
                                        name: 'max-width',
                                        id: 'max-width',
                                    }}
                                >
                                    {/* <MenuItem value="">None</MenuItem> */}
                                    {sessionData && sessionData.map((item, index) => {
                                        return <MenuItem key={index} value={item.attributes.session_id}>{`${item.attributes.session_id} - ${moment(item.attributes.scheduled_at).format('DD MMMM, YYYY')}`}</MenuItem>
                                    })}
                                </Select>
                            </FormControl>
                        </Box>
                    )}
                    <DialogContentText>
                        {notesRef.current && notesRef.current.map((item, index) => (
                            <Grid item lg={12} md={6} sm={12} xs={12} sx={{ mt: 2 }}>
                                <TextField
                                    key={`${index}`}
                                    fullWidth
                                    multiline
                                    rows={3}
                                    inputProps={{ readOnly: true }}
                                    defaultValue={item.details}
                                />
                                <Grid container style={{ display: 'flex', justifyContent: 'space-between' }}>
                                    <Grid >{item.added_by}</Grid>
                                    <Grid >{moment(item.updatedAt).format('DD/MM/YYYY hh:mm A')}</Grid>

                                </Grid>
                            </Grid>
                        ))}
                        {!notes && sessionData && <Grid mt={2}>No Record Found</Grid>}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button variant="contained" type="submit" onClick={closeModal}>Close</Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}

export default NotesModal