import moment from 'moment';
import { formatTime } from './utils';
export function formatCenters(data) {
    let response = [];
    const meta = {
        centerId: "Center ID",
        name: "Full Name",
        email: "Email",
        phoneNumber: "Phone Number",
        state: "State",
        centerType: "Center Type",
        // verified: "Status",
        dateCreated: "Date Created",

    }
    if (Array.isArray(data)) {
        // data.map((item) => {
        //     response.push(formatData(item))
        // })
        data.forEach(item => {
            response.push(formatData(item))
        })
    }
    else {
        response.push(formatData(data))
    }
    response["meta"] = meta
    return response;
}

export function formatCounsellor (data) {
    let response = [];
    const meta = {
        counsellorId: "Provider ID",
        name: "Full Name",
        email: "Email ID",
        phoneNumber: "Phone Number",
        dateCreated: "Date Created",

    }
    if (Array.isArray(data)) {
        data.forEach((item) => {
            const obj = {
                id: item && item.user && item.user.username,
                name: item.name,
                email: item.email,
                phoneNumber: item.phone_number.toString(),
                createdAt: moment(item.createdAt).format('DD MMMM, YYYY')
            };
            response.push(obj)
        })
    }
    else {
        const obj = {
            id: data && data.user && data.user.username,
            name: data.name,
            email: data.email,
            phoneNumber: data.phone_number,
            createdAt: moment(data.createdAt).format('DD MMMM, YYYY')
        };
        response.push(obj)
    }
    response["meta"] = meta
    return response;
}

