export function formatSession(data, type=null) {
    let response = [];
    let meta
    if (type && type !== 'provider') {
        meta = {
            counsellor_name: "Provider Name",
            caseId: "Case ID",
            sessionId: "Session ID",
            scheduledAt: "Scheduled At",
            duration: "Duration",
            status: "Status"
    
        }
        data.forEach((item) => {
            const obj = {
                counsellor_name: item && item.counsellor && item.counsellor.name,
                caseId: item.case_id,
                sessionId: item.session_id,
                scheduledAt: moment(item.scheduled_at).format('LLL'),
                duration: formatTime(item.duration),
                status: item.status
            };
            response.push(obj)
        })
    } else {
        meta = {
            // counsellor_name: "Provider Name",
            caseId: "Case ID",
            sessionId: "Session ID",
            scheduledAt: "Scheduled At",
            duration: "Duration",
            status: "Status"
    
        }
        data.forEach((item) => {
            const obj = {
                // counsellor_name: item && item.counsellor && item.counsellor.name,
                caseId: item.case_id,
                sessionId: item.session_id,
                scheduledAt: moment(item.scheduled_at).format('LLL'),
                duration: formatTime(item.duration),
                status: item.status
            };
            response.push(obj)
        })
    }
    
    response['meta']= meta
    return response;
}

export function formatDetails(data) {
    let icon = 'http://localhost:1337'+data[(data.role.name).toLowerCase()].logo_image

    const response = {
        UserID: data.username,
        name: getName(data),
        phoneNumber: data[(data.role.name).toLowerCase()].phone_number,
        // username: data.username,
        email: data.email,
        verified: data.confirmed,
        logo:icon,
        date_created: moment(data.createdAt).format('DD MMMM, YYYY'),
        role: data.role.name
    };

    return response
}

