function getName(data) {
    let fullName = ''
    switch (data.role.name) {
        case "Center":
            fullName = data.center && data.center.name ? data.center.name : ''
            break;
        case "Counsellor":
            fullName = data.counsellor && data.counsellor.name ? data.counsellor.name : ''
            break;
        case "Client":
            fullName = data.client && data.client.name ? data.client.name: ''
            break;
        case "Admin":
            fullName = data.profile && data.profile.first_name ? data.profile.first_name + data.profile.last_name : ''
            break;
        default:
            fullName = "NIL"
            break;
    }
    return fullName
}