import React, { useEffect, useState, useCallback } from 'react'
import {
    CircularProgress,
    IconButton, Badge
} from '@mui/material'
import PreviewIcon from '@mui/icons-material/Preview';
import TableCell from '@mui/material/TableCell'
import { useNavigate } from 'react-router-dom'
import './Centers.css'
import moment from 'moment';
import useAuth from 'app/hooks/useAuth';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import UsersCustomTable from 'app/components/UsersCustomTable/UsersCustomTable';
import CustomTable from 'app/components/common/CustomTable';
import { ClientSearchFilter } from '../SearchFilter/ClientSearchFilter';
import { ProfileCard, Breadcrumb, SearchInput, UserProfileDetail } from 'app/components';
import { formatSession } from '../../../utils/counseler.data.formater'
import { formatCounsellor } from 'app/utils/data.formater';
import AppointmentListTable from 'app/components/AppointmentListTable/AppointmentListTable';
import { ClientListTable } from 'app/components';
import { getCenterDetails } from 'app/services/centerService';
import SessionModal from 'app/components/SessionModal/SessionModal';
import { tabsData, tabsCounselorData } from 'app/constants/data';

import { styled } from '@mui/system';
import '../../users/Users.css';

const Container = styled('div')(({ theme }) => ({
    margin: '30px',
    [theme.breakpoints.down('sm')]: {
        margin: '16px',
    },
    '& .breadcrumb': {
        marginBottom: '30px',
        [theme.breakpoints.down('sm')]: {
            marginBottom: '16px',
        },
    },
}))

const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    top: '6px',
    left: '25px',
}))

const CenterDetails = () => {
    const [loading, setLoading] = useState(false)
    const [value, setValue] = useState('1');
    const [valueAppointmentTab, setValueAppointmentTab] = useState('1');
    const [tab, setTab] = useState([])
    const [tabAppointment, setTabAppointment] = useState([
        {
            id: 1,
            name: 'Upcoming'
        },
        {
            id: 2,
            name: 'History'
        }
    ])
    const [detailsData, setDetailsData] = useState('')
    const [counsellorData, setCounsellorData] = useState('')
    const [centerData, setCenterData] = useState('')
    const [clientData, setClientData] = useState('')
    const [appointmentHistoryData, setAppointmentHistoryData] = useState('')
    const [appointmentUpcomingData, setAppointmentUpcomingData] = useState('')
    const [rowsPerPage, setRowsPerPage] = React.useState(5)
    const [page, setPage] = React.useState(0)
    const [searchResult, setSearchResult] = useState(false)
    const [logo, setLogo] = useState('')
    const [searchedData, setSearchedData] = useState({
        center: [],
        counsellor: [],
        client: [],
        appointment: [],
    })
    const [values, setValues] = useState({
        center: '',
        counsellor: '',
        client: '',
        appointment: '',
    })
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState(''); 
    const [sessionId, setSessionId] = useState([])
    const [modalVisible, setModalVisible] = useState(false);
    const navigate = useNavigate();
    const { user } = useAuth();
    const id = window.location.href.split('/').pop();

    const handleChangePage = (event, newPage) => {
        setPage(newPage)
    }

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value)
        setPage(0)
    }

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    const handleAppointmentTabChanged = (e, newValue) => {
        setValueAppointmentTab(newValue)
    }

    const handleBlur = (data, type, pageNumber) => {
        // let value = e.target.value
        // console.log(value, prop, clientData)
        // setValues({ ...values, [prop]: value })
        // console.log(searchResult)
        let searchData = clientData.filter((item) => {
            return item.clientId.toLowerCase().includes(data.toLowerCase()) || item.name.toLowerCase().includes(data.toLowerCase()) || item.gender.toLowerCase().includes(data.toLowerCase()) || item.traumas.toLowerCase().includes(data.toLowerCase()) || item.ethnicity.toLowerCase().includes(data.toLowerCase())
        })
        searchData['meta'] = clientData.meta
        setSearchedData({ ...searchedData, client: searchData })
        // setSearchResult(true)
        // if (data && data.length > 2) {
        //     let searchData = clientData.filter((item) => {
        //         return item.clientId.toLowerCase().includes(data.toLowerCase()) || item.name.toLowerCase().includes(data.toLowerCase()) || item.gender.toLowerCase().includes(data.toLowerCase()) || item.traumas.toLowerCase().includes(data.toLowerCase()) || item.ethnicity.toLowerCase().includes(data.toLowerCase())
        //     })

        //     // console.log(searchData)
        //     // setSearchResult(true)
        //     searchData['meta'] = clientData.meta
        //     setSearchedData({ ...searchedData, client: searchData })
        // } else {
        //     // setSearchResult(false)
        // }
    }

    const fetchData = async () => {
        let id = window.location.href.split('/').pop();
        const res = await getCenterDetails(id)
        return res
    }

    function getSessions(data) {
        const sessionsArr = data.filter(item => {
            return item.counsellor && item.counsellor.id === user.counsellor.id
        })
        return sessionsArr && sessionsArr
    }

    const makeAppointmentData = (data) => {
        let upcomingAppointment = []
        let historyAppointment = []
        const td_start = moment().startOf('minute')
        const td_end = moment().endOf('day')
        console.log(data)
        const sessions = data && getSessions(data)
        sessions && sessions.map((item) => {
            if (moment(moment(item.scheduled_at).format('x'), 'x').isAfter(td_start)) {
                upcomingAppointment.push(item)
            } else {
                historyAppointment.push(item)
            }
        })
        setAppointmentUpcomingData(formatSession(upcomingAppointment, 'upcoming'))
        setAppointmentHistoryData(formatSession(historyAppointment, 'history'))

    }

    function formatDetails(data) {

        const response = {
            centerId: data.user ? data.user.username : '-',
            name: data.name ? data.name : '-',
            email: data.user?.email ? data.user.email : '',
            phoneNumber: data.area_code != null ? data.area_code + '-' + data.phone_number : data.phone_number,
            date_created: moment(data.createdAt).format('DD MMMM, YYYY'),
        };

        return response
    }
    const getTrauma = (data) => {
        let traumaCode
        // console.log(data)
        const code = data.map((item) => {
            return item.trauma_code
        })
        traumaCode = code.join(', ')

        return traumaCode
    }

    function getClients(data) {
        const clientsArr = data.filter(item => {
            return item.counsellor && item.counsellor.id === user.counsellor.id
        })
        return clientsArr && clientsArr
    }
    const formatClient = (data) => {
        const clients = data && getClients(data)
        let response = [];
        const meta = {
            // id: "ID",
            clientId: "Client ID",
            name: 'Record Name',
            // counsellor: "Counselor",
            gender: "Birth Gender",
            // idGender: "ID Gender",
            traumas: "Trauma Codes",
            ethnicity: "Ethnicity",
            status: "Status"

        }
        clients && clients.forEach((item) => {
            let traumas = item.traumas ? getTrauma(item.traumas) : ''
            const obj = {
                // id: item.id,
                clientId: item.client_id,
                name: item.name,
                // counsellor: counsellor ? counsellor.name : '',
                gender: item.gender_bio,
                // idGender: item.gender_identified,
                traumas: traumas,
                ethnicity: item.ethnicity ? item.ethnicity : '',
                status: item.status ? item.status.replace('_', ' ').replace(/([a-z])([A-Z])/g, '$1 $2') : '-'
            };
            response.push(obj)
        })
        response['meta'] = meta
        return response;
    }

    const getCellContent = (key, value) => {
        let content = null;
        switch (key) {
            case 'view_notes':
                content = value;
                break;
            default:
                content = value;
                break;
        }

        return content;
    }

    useEffect(() => {
        setLoading(true)
        setTab(tabsCounselorData.center)
        fetchData(id).then((res) => {
            if (res) {
                if(res?.error?.name === 'UnauthorizedError'){
                    setOpenModal(true);
                    setMsg('Your session has expired. You will be logged out.');
                } else {
                    setCounsellorData(formatCounsellor(res.counsellors))
                    // setCenterData(formatCenters(res.center))
                    setClientData(formatClient(res.clients))
                    // if (moment(moment(item.scheduled_at).format('x'), 'x').isBetween(td_start, td_end)) {
                    //     data.push(item)
                    // }
                    setDetailsData(formatDetails(res))
                    makeAppointmentData(res.sessions)
                }
            }
            if (res.logo_image) {
                let logo = 'http://localhost:1337' + res.logo_image
                setLogo(logo)
            }
            setLoading(false)
        })
            .catch(console.error)

    }, [])

    const Client = () => {
        const viewNotes = (value) => {
            // console.log(value)
            return (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                    <IconButton
                        aria-label="view"
                        color="primary"
                        onClick={(e) => handleNotes(e, value, true)} //props.handleOpen(value, true) }
                    ><Badge badgeContent={value.notes ? value.notes.length : 0} color="secondary">
                            <PreviewIcon /></Badge>
                    </IconButton>
                </div>
            );
        }

        const getCellContent = (key, value) => {
            let content = null;
            switch (key) {
                case 'view_notes':
                    content = viewNotes(value);
                    break;
                default:
                    content = value;
                    break;
            }

            return content;
        }

        const handleNotes = (e = null, id, visible) => {
            // e && e.stopPropagation()
            setSessionId(id);
            setModalVisible(visible);
        }

        return <>
            <ClientSearchFilter
                filterClient={handleBlur}
                fetchClientList={() => setSearchResult(false)}
                searchResult={() => setSearchResult(true)}
                searchClient={(e,f,g) => handleBlur(e,f,g)}
                page={page}
            />
            {/* <SearchInput placeholder="Search Client" value={values.client} handleBlur={handleBlur('client')} /> */}
            {clientData && <ClientListTable
                role={user.role.name}
                data={searchResult && searchedData.client || clientData}
                getCellContent={getCellContent}
            // onClick={goToDetails}
            />}
            {/* <ClientSearchFilter 
                filterClient={filterClient}
                fetchClientList={fetchClientList}
                searchResult={setSearchResult}
                searchClient={searchClient}
                page={page}
            />
            <UsersCustomTable role="Client" rowsPerPage={rowsPerPage} page={page} handleChangeRowsPerPage={handleChangeRowsPerPage} handleChangePage={handleChangePage} rows={searchResult && values.client !== '' ? searchedData.client : clientData} /> */}
        </>
    }

    const UpcomingAppointments = () => {
        return <>
            {/* <SearchInput placeholder="Search Appointment" value={values.appointment} handleBlur={handleBlur('appointment')} /> */}
            {/* <UsersCustomTable role="Session" rowsPerPage={rowsPerPage} page={page} handleChangeRowsPerPage={handleChangeRowsPerPage} handleChangePage={handleChangePage} rows={searchResult && values.appointment !== '' ? searchedData.appointment : appointmentUpcomingData} /> */}
            {appointmentUpcomingData &&
                <AppointmentListTable
                    role={user.role.name}
                    data={appointmentUpcomingData}
                    loading={loading}
                    fetchData={fetchData}
                    user={user}
                // getCellContent={getCellContent}
                // onClick={goToDetails}
                />
            }
        </>
    }

    const HistoryAppointments = () => {
        return <>
            {/* <SearchInput placeholder="Search Appointment" value={values.appointment} handleBlur={handleBlur('appointment')} /> */}
            {/* <UsersCustomTable role="Session" rowsPerPage={rowsPerPage} page={page} handleChangeRowsPerPage={handleChangeRowsPerPage} handleChangePage={handleChangePage} rows={searchResult && values.appointment !== '' ? searchedData.appointment : appointmentHistoryData} /> */}

            {appointmentUpcomingData &&
                <AppointmentListTable
                    role={user.role.name}
                    data={appointmentHistoryData}
                    loading={loading}
                    fetchData={fetchData}
                    user={user}
                // getCellContent={getCellContent}
                // onClick={goToDetails}
                />
            }
        </>
    }

    const Appointment = () => {
        return <>
            <Box sx={{ padding: 0, margin: 0 }}>
                <TabContext value={valueAppointmentTab}>
                    <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                        <TabList onChange={handleAppointmentTabChanged} aria-label="Appointment Details Segement">
                            {tabAppointment.map((item, index) => (
                                <Tab label={item.name} value={item.id.toString()} key={index} />
                            ))}
                        </TabList>
                    </Box>
                    {tabAppointment && tabAppointment.map((item, index) => {
                        return (
                            <TabPanel key={index} value={item.id.toString()}>
                                {item.id === 1 ? (
                                    <UpcomingAppointments />
                                ) : (
                                    <HistoryAppointments />
                                )}
                            </TabPanel>
                        )
                    })}

                </TabContext>
            </Box>
        </>
    }

    return <Container>
        <Breadcrumb
            routeSegments={[
                { name: "All Centers", path: "/centers-list" },
                { name: "Center Details" }
            ]}
        />
        {!loading && detailsData !== '' && (
            <TabContext value={value}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <TabList onChange={handleChange} aria-label="Center Details Segement">
                        {tab.map((item, index) => (
                            <Tab label={item.name} value={item.id.toString()} key={index} />
                        ))}
                    </TabList>
                </Box>
                {tab && tab.map((item, index) => {
                    return (
                        <TabPanel key={index} value={item.id.toString()}>
                            {item.id === 1 ? (
                                <ProfileCard
                                    title={detailsData.name}
                                    subtitle={"Center"}
                                    icon={logo}
                                    action={false}
                                // anchorEl={anchorEl}
                                // open={open}
                                // handleClick={handleClick}
                                // handleClose={handleClose}
                                // navigateTo={pageNavigate}
                                // assignCenter={assignCenter}
                                >
                                    <UserProfileDetail data={detailsData} />
                                </ProfileCard>
                            ) : item.id === 2 ? (
                                // <Client />
                                <>
                                {/* <SearchFilter
                                    handleSearch={handleBlur('client')}
                                    handleChange={handleClientChange}
                                    filterValue={clientFilterValue}
                                    genderValue={filterValueGender}
                                    multiFilter={true}
                                    selectValue={queryRef.current.ethnicity}
                                    genderSelectValue={queryRef.current.gender}
                                    searchValue={queryRef.current.search}
                                    label="Ethnicity"
                                    searchLabel="Search Client"
                                    type={type}
                                    handleReset={handleReset}
                                /> */}
                                {clientData && <ClientListTable
                                role={user.role.name}
                                data={searchResult ? searchedData.client : clientData}
                                getCellContent={getCellContent}
                            // onClick={goToDetails}
                                />}
                                </>
                            ) : item.id === 4 ? (
                            <Client />
                            ) : (
                            <Appointment />
                            )}
                        </TabPanel>
                    )
                })}

            </TabContext>
        )}
        <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
    </Container>
}

export default CenterDetails