import moment from 'moment';

function getTraumas(data){
    let traumaCode = ''
    const code = data.map((item) => {
        return item.trauma_code
    })
    traumaCode = code.join(', ')
    return traumaCode
}

const getSession = (data, clientId, center,type) => {
    let sessions = data ? data : null
    let obj
    if(type && type === 'upcoming'){
        obj = {
            id: data.id,
            status : sessions.status === 'scheduled' && moment(moment(sessions.scheduled_at).format('x'), 'x').isBefore(moment().startOf('second')) && 'missed' || sessions.status,
            scheduledAt: `${moment(sessions.scheduled_at).format('DD MMMM, YYYY hh:mm A')}`,
            sessionId: sessions.session_id,
            clientId: clientId,
            center: center,
            disabled: data.disabled
        }
    } else {
        obj = {
            id: data.id,
            status : (sessions.status === 'scheduled' || sessions.status === 'pending') && 'missed' || sessions.status,
            scheduledAt: `${moment(sessions.scheduled_at).format('DD MMMM, YYYY hh:mm A')}`,
            sessionId: sessions.session_id,
            clientId: clientId,
            center: center,
            disabled: data.disabled
        }
    }
    
    return obj
    // sessions.scheduled_at && sessions.status == 'scheduled' ? `${moment(sessions.scheduled_at).format('DD MMMM, YYYY hh:mm')} CST` : (sessions.status == 'pending' ? 'pending' : sessions.status == 'cancelled' ? 'Cancelled' : '-')
}
export function formatSession(data,type = null) {
    let response = [];
    const meta = {
        // id: "ID",
        clientId: "Client ID",
        name: "Record Name",
        center: "Center",
        gender: "Birth Gender",
        // idGender: "ID Gender",
        traumas: "Trauma Codes",
        ethnicity: "Ethnicity",
        scheduledAt: "Scheduled",
        view_notes: "Notes"

    }
    data.forEach((item) => {
        const obj = {
            // id: item.id,
            clientId: item?.client?.client_id,
            name: item?.client?.name,
            center: item?.center?.name,
            gender: item?.client?.gender_bio,
            // idGender: item.client.gender_identified,
            traumas: item?.client?.traumas ? getTraumas(item?.client?.traumas) : '',
            ethnicity: item?.client?.ethnicity ? item?.client?.ethnicity : '',
            // scheduledAt: item.scheduled_at && item.status !== 'pending' ? moment(item.scheduled_at).format('DD MMMM, YYYY hh:mm') + ' CST' : 'pending',
            scheduledAt: getSession(item,item?.client?.client_id, item?.center?.name, type),
            // view_notes: item.notes ? item.notes : ''
            view_notes: {
                clientId: item?.client?.client_id,
                clientName: item?.client?.name,
                sessionId:item?.id,
                center_id: item?.center?.id,
                notes: item?.notes
                // owned_by: item.attributes.owned_by,
            },
        };
        response.push(obj)
    })
    response['meta']= meta
    return response;
}

const clinicianMeta = {
    clininicain_id: "Provider ID",
    profesional_name: "Professional Name",
    first_name: 'First Name',
    last_name: "Last Name",
    traumas: "Trauma Codes",
    status: "Status",
    clients:"Clients",
    sessions: "Sessions",
    disable_provider: "Disable Provider"
}

export function formatClinicians(data) {
    const response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(cliniciansData(item))
        })
        response['meta'] = clinicianMeta;
    } else {
        response.push(cliniciansData(data))
    }
    return response
}

function getTraumaCodes(data) {
    let traumaCode = ''
    const code = data.map((item) => {
        return item.attributes.trauma_code
    })
    traumaCode = code.join(', ')
    return traumaCode
}

function cliniciansData(item) {
    const obj = {
        clinicId:item?.id || "-",
        clininicain_id: (item && item.attributes?.user?.data?.attributes?.username) || '-',
        profesional_name: (item && item.attributes && item.attributes.name) || '-',
        first_name: (item && item.attributes && item.attributes.first_name) || '-',
        last_name:(item && item.attributes && item.attributes.last_name) || '-',
        traumas:(item && item.attributes && item.attributes.traumas && getTraumaCodes(item.attributes.traumas?.data)) || '-',
        status: item?.attributes?.status || "-",
        clients:(item && item.attributes && item.attributes.clients && item.attributes.clients.data && item.attributes.clients.data?.length) || '-',
        sessions:(item && item.attributes && item.attributes.sessions && item.attributes.sessions.data && item.attributes.sessions?.data?.length) || '-',
        disable_provider: {
            provider_id: item?.id,
            disabled: item?.attributes?.user?.data?.attributes?.blocked || false
        }
    }

    return obj;
}


export function formatSessions(data){
    const sessions = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            sessions.push(SessionData(item))
        })
    } else {
        sessions.push(SessionData(data))
    }
    return sessions
}
function SessionData(item) {
    const obj = {
        title: (item?.client?.name+"-"+item?.client?.client_id) || "Anonymous", 
        start: moment(item?.scheduled_at).toDate(),
        end: moment(item?.scheduled_at).add(50,'minutes').toDate(),
        session:item
    }
    return obj
}