import React, {useState} from 'react'
import {
    Grid,
    IconButton, Badge,
    Button,
    TableCell,
    Chip
} from '@mui/material'
import PreviewIcon from '@mui/icons-material/Preview';
import VideocamIcon from '@mui/icons-material/Videocam';
import CustomTable from '../common/CustomTable';
import NotesModal from '../CustomModal/NotesModal'
import AcceptModal from '../CustomModal/AcceptModal'
import RejectModal from '../CustomModal/RejectModal'
import { updateSessionStatus } from 'app/services/counselorService';
import AddIcon from '@mui/icons-material/Add';

const AppointmentListTable = (props) => {
    const [sessionId, setSessionId] = useState([])
    const [acceptModalData, setAcceptModalData] = useState()
    const [rejectModalData, setRejectModalData] = useState()
    const [modalVisible, setModalVisible] = useState(false);
    const [acceptModalVisible, setAcceptModalVisible] = useState(false);
    const [rejectModalVisible, setRejectModalVisible] = useState(false);
    const [loading, setLoading] = useState(false)
    // const [rejectReason, setRejectReason] = useState('')
    // const [page, setPage] = useState(0)

    const [rowsPerPage, setRowsPerPage] = React.useState(10)
    const [page, setPage] = useState(0)

    const handleChangePage = (event, newPage) => {
        setPage(newPage)
        // if (!searchResult) {
        //     try {
        //         let pageNumber = (Number(newPage)) === 0 ? 0 : (Number(newPage) - 1) + 10
        //         fetchData(pageNumber)
        //             .then((resp) => {
        //                 setCounsellorData(formatCounsellor(resp))
        //                 // setRootRows(resp)
        //             })
        //             .catch(err => {
        //                 console.log("ERROR", err)
        //             })
        //     } catch (error) {
        //         console.log("ERROR", error)
        //     }
        // }
    }

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value)
        setPage(0)
    }

    const handleSubmit = (e, data) => {
        setLoading(true)
        const options = {
            data: {
                status: 'scheduled',
            },
        }
        updateSessionStatus(data.data.id, options)
            .then((resp) => {
                setLoading(false)
                setAcceptModalVisible(false);
                props.fetchData(page, props.user.counsellor.id)
            })
            .catch(err => console.log(err))
    }
    const handleRejectSubmit = (data, rejectReason) => {
        setLoading(true)
        const options = {
            data: {
                status: 'cancelled',
                cancell_reason: rejectReason
            },
        }
        updateSessionStatus(data.data.id, options)
            .then((resp) => {
                if (resp) {
                    setRejectModalVisible(false);
                    props.fetchData(page, props.user.counsellor.id)
                }
                setLoading(false)
            })
            .catch(err => console.log(err))
    }

    const handleNotes = (id, visible) => {
        setSessionId(id);
        setModalVisible(visible);
    }

    const handleAcceptModal = (data, visible) => {
        // setSessionId(id);
        setAcceptModalData(data)
        setAcceptModalVisible(visible);
    }

    const handleRejectModal = (data, visible) => {
        // setSessionId(id);
        setRejectModalData(data)
        setRejectModalVisible(visible);
    }

    const viewNotes = (value) => {
        // console.log(value)
        return (
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <IconButton
                    aria-label="view"
                    color="primary"
                    onClick={() => handleNotes(value, true)} //props.handleOpen(value, true) }
                ><Badge badgeContent={value.notes ? value.notes.length : 0} color="secondary">
                        <PreviewIcon /></Badge>
                </IconButton>
                {props.addNotes && <IconButton
                    aria-label="view"
                    color="primary"
                    onClick={() => props.handleOpenNewNotes(value, true)}
                >
                   <AddIcon />
                </IconButton> }
            </div>
        );
    }
    const scheduleAppointment = (value) => {
        if (value.status === 'scheduled') {
            return <div style={{ marginLeft: '1rem' }}>{value.scheduledAt}<IconButton aria-label="view" color="primary">{props.user.role.type === 'counsellor' && <VideocamIcon />}</IconButton></div>
            // onClick={() => props.handleOpen(value, true)}

        } else if (value.status === 'pending') {
            return (
                <div>
                    <div style={{ marginBottom:10 }}>{`${value.scheduledAt}`}</div>
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                    <Button
                    disabled={value.disabled}
                        variant="contained"
                        color="success"
                        style={{ marginRight: 5 }}
                        onClick={() => handleAcceptModal(value, true)}
                    // startIcon={<VisibilityIcon />}
                    >
                        <div style={{ fontSize: '.8rem' }}>Accept</div>
                    </Button>
                    <Button
                        variant="contained"
                        color="error"
                        onClick={(e) => handleRejectModal(value, true)}
                    // startIcon={<VisibilityIcon />}
                    >
                        <div style={{ fontSize: '.8rem' }}>Reject</div>
                    </Button>
                </div>
                </div>
            )
        } else if (value.status === 'cancelled') {
            return <div style={{ marginLeft: '1rem' }}>{`${value.scheduledAt}`}<Chip label="Cancelled" size="small" variant="outlined" color="error"/></div>
        } else if (value.status === 'missed') {
            return <div style={{ marginLeft: '1rem' }}>{`${value.scheduledAt}`}<Chip label="Missed" size="small" variant="outlined" color="error"/></div>
        }
        else {
            return <div style={{ marginLeft: '1rem' }}>{value.scheduledAt}<Chip label={value.status === 'completed' && 'Completed' || value.status} size="small" variant="outlined" color="success"/></div>
        }
    }

    const getCellContent = (key, value) => {
        let content = null;
        switch (key) {
            case 'view_notes':
                content = viewNotes(value);
                break;
            case 'scheduledAt':
                content = scheduleAppointment(value);
                break;
            default:
                content = value;
                break;
        }

        return content;
    }

    const CustomHeadCell = ({ meta }) => {
        const data = [];
        let entries = Object.entries(meta)
        for (const [index, [key, value]] of Object.entries(entries)) {
            key !== 'id' && data.push(<TableCell width={key === 'scheduledAt' ? 140 : 60} align="center" className="text-style" key={index}>{value}</TableCell>)
        }
        return data
    }
    

    const CustomBodyCell = ({ row }) => {
        const rows = [];
        let entries = Object.entries(row)
        for (const [index, [key, value]] of Object.entries(entries)) {
            key !== 'id' && rows.push(<TableCell className="text-style" component="th" scope="row" align="center" key={index}>{getCellContent(key, value)}</TableCell>)
        }
        return rows
    }
    return (
        <>
            <Grid>
                {props.role === 'Provider' ? (
                    <Grid>
                        <CustomTable
                            // onClick={props.onClick}
                            page={page}
                            handleChangePage={handleChangePage}
                            rowsPerPage={rowsPerPage}
                            handleChangeRowsPerPage={handleChangeRowsPerPage}
                            rows={props.data}
                            CustomHeadCell={CustomHeadCell}
                            CustomBodyCell={CustomBodyCell}
                        />
                    </Grid>
                ) : (
                    <Grid>

                    </Grid>
                )}
                <NotesModal
                    open={modalVisible}
                    data={sessionId}
                    multipleSession={false}
                    handleClose={() => {
                        handleNotes(null, false);
                        //handlePageChange(null, 0);
                    }}
                />
                <AcceptModal
                    open={acceptModalVisible}
                    data={acceptModalData}
                    handleClose={() => handleAcceptModal(null, false)}
                    handleSubmit={handleSubmit}
                    loading={loading}
                // currentClient={currentClient}
                />
                <RejectModal
                    open={rejectModalVisible}
                    data={rejectModalData}
                    handleClose={() => handleRejectModal(null, false)}
                    handleSubmit={handleRejectSubmit}
                    loading={loading}
                // reason={rejectReason}
                // handleReasonChange={handleReasonChange}
                // currentClient={currentClient}
                />
            </Grid>
        </>
    )
}

export default AppointmentListTable