import React from 'react'
import {
    Grid,
    Button,
    Box,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
} from '@mui/material'
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import './common.css'

const SearchFilter = (props) => {
    const renderMenuItem = (key) => {
        if (key == 'Gender') {
            return props.genderValue.map((item, index) => {
                return <MenuItem key={index} value={item}>{item}</MenuItem>
            })
        } else {
            return props.filterValue.map((item, index) => {
                return <MenuItem key={index} value={item}>{item}</MenuItem>
            }
            )
        }
    }
    const renderFilters = () => {
        if (props.multiFilter) {
            return Object.entries(props.type).map(([key, value], i) => {
                return <Grid item lg={6} md={6} sm={12} xs={12} key={`${i}`}>
                    <Box>
                        <FormControl fullWidth>
                            <InputLabel sx={{ backgroundColor: '#fafafa', padding: '0.25rem' }}>{key}</InputLabel>
                            <Select
                                id="demo-simple-select"
                                value={key == 'Gender' ? props.genderSelectValue : props.selectValue}
                                label="Age"
                                onChange={(e) => props.handleChange(e, key)}
                                placeholder="Search Center"
                            >
                                {renderMenuItem(key)}
                            </Select>
                        </FormControl>
                    </Box>
                </Grid>
            }
            )
        } else {
            return <Grid item lg={6} md={6} sm={12} xs={12}>
                <Box>
                    <FormControl fullWidth>
                        <InputLabel sx={{ backgroundColor: '#fafafa', padding: '0.25rem' }}>{props.label}</InputLabel>
                        <Select
                            id="demo-simple-select"
                            value={props.selectValue}
                            label="Age"
                            onChange={(e) => props.handleChange(e)}
                            placeholder="Search Center"
                        >
                            {props.filterValue && props.filterValue.map((item, index) => {
                                return <MenuItem key={index} value={item}>{item}</MenuItem>
                            })}
                        </Select>
                    </FormControl>
                </Box>
            </Grid>
        }
    }
    return (
        <>
            <Grid sx={{ marginTop: '0px', marginBottom: '20px' }} container rowSpacing={3} columnSpacing={6}>
                <Grid item lg={6} md={6} sm={12} xs={12}>
                    {/* <FormControl fullWidth> */}
                        <TextField
                            sx={{width: '100%'}}
                            label={props.searchLabel}
                            variant="outlined"
                            // className="client-search"
                            onChange={props.handleSearch}
                            
                            placeholder={props.searchLabel}
                            type="text"
                            value={props.searchValue}
                            autoFocus
                            InputProps={{
                                startAdornment: <SearchIcon />,
                            }}
                        />
                    {/* </FormControl> */}
                </Grid>
                {renderFilters()}
            </Grid>
            <FormControl sx={{ marginBottom: '20px', display: 'flex', width: '100%' }}>
                <Button
                    color="primary"
                    variant='contained'
                    onClick={props.handleReset}
                    sx={{ alignSelf: 'flex-end' }}
                >
                    Reset
                </Button>
            </FormControl>
        </>

    )
}

export default SearchFilter