import React, { useState, useEffect } from 'react';
import useStateRef from 'react-usestateref';
import SearchFilter from 'app/components/common/SearchFilter';
import useAuth from 'app/hooks/useAuth';

export const ClientSearchFilter = ({ searchClient, filterClient, fetchClientList, searchResult, page }) => {
    const { user } = useAuth();
    const [type, setType] = useState({
        Gender: 'All',
        Ethnicity: 'All'
    });
    const [queryData, setQueryData, queryRef] = useStateRef({
        search: '',
        ethnicity: 'All',
        gender: 'All'
    })
    const [filterValue, setFilterValue] = useState([
        'All', 'American Indian', 'Alaska Native', 'Asian', 'Black or African American', 'Hispanic or Latino', 'Native Hawaiian', 'Pacific Islander', 'White', 'Unknown'
    ])
    const [filterValueGender, setFilterValueGender] = useState([
        'All', 'Male', 'Female', 'Other'
    ])

    const handleChange = ({ target: { value } }, type) => {
        if (type == 'Gender') {
            setQueryData({ ...queryData, gender: value })
        } else if (type == 'Ethnicity') {
            setQueryData({ ...queryData, ethnicity: value })
        }
        if (value !== 'All') {
            searchResult(true)
            filterClient(value, page, type);
        }
        else {
            searchResult(false)
            fetchClientList(page, user.counsellor.id)
        }

    }
    const handleReset = () => {
        setType({
            Gender: 'All',
            Ethnicity: 'All'
        });
        setQueryData({
            search: '',
            ethnicity: 'All',
            gender: 'All'
        });
        fetchClientList(page, user.counsellor.id)
    }

    const handleSearch = async ({ target: { value } }) => {
        setQueryData({ ...queryData, search: value })
        if (value && value.length > 2) {
            // setQueryData({ ...queryData, search: value })
            // setSearchData({...searchData, search: value})
            searchClient(value, type, page);
            searchResult(true)
        } else if (value === "") {
            fetchClientList(page, user.counsellor.id);
            searchResult(false)
        }
    }

    return (
        <SearchFilter
            handleSearch={handleSearch}
            handleChange={handleChange}
            filterValue={filterValue}
            genderValue={filterValueGender}
            multiFilter={true}
            selectValue={queryRef.current.ethnicity}
            genderSelectValue={queryRef.current.gender}
            searchValue={queryRef.current.search}
            label="Ethnicity"
            searchLabel="Search Client"
            type={type}
            handleReset={handleReset}
        />
    )
}