import React, { useRef, useState, useEffect } from 'react'
// import MenuItem from '@mui/material/MenuItem'
import { styled } from '@mui/system'
import FormControl from '@mui/material/FormControl'
import {
    Box,
    Button,
    Modal,
    Typography,
    CircularProgress,
    Grid,
    TextField
} from '@mui/material'

import { ValidatorForm } from 'react-material-ui-form-validator'
import moment from 'moment'

const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    bottom: '38px',
    right: '60px',
}))

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    // width: '100%',
    bgcolor: 'background.paper',
    border: '2px solid #fff',
    boxShadow: 24,
    p: 4,
    borderRadius: '15px'
}

const RejectModal = (props) => {
    const [sessionId, setSessionId] = useState('')
    const [clientId, setClientId] = useState('')
    const [center, setCenter] = useState('')
    const [loading, setLoading] = useState(false)
    const [data, setData] = useState()
    const [rejectReason, setRejectReason] = useState('')
    const [error, setError] = useState(false)
    const closeModal = () => {
        setRejectReason('')
        setError('')
        props.handleClose();
    }
    function handleSubmit(e, data) {
        console.log(rejectReason)
        if (rejectReason.length === 0) {
            setError(true)
            return
        }
        setError(false)
        props.handleSubmit(data, rejectReason)
    }

    useEffect(() => {

        ValidatorForm.addValidationRule('REQUIRED', (value) => {
            if (value.length === 0) {
                return false;
            }
            return true;
        })
        if (props.data) {
            console.log(props.data)
            setSessionId(props.data.sessionId)
            setCenter(props.data.center)
            setClientId(props.data.clientId)
            setData(props.data)
            props.data && props.data.disabled && setRejectReason('Appointment cancelled, time slot not available.')
        }

        return () => {
            ValidatorForm.removeValidationRule('REQUIRED');
        }
    }, [props])
    return (
        <div>
            <Modal
                keepMounted
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    <ValidatorForm
                        // ref={formEl}
                        onSubmit={(e) => handleSubmit(e, props)}
                        onError={() => null}
                    >
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="h6"
                            component="h2"
                        >
                            Reject this appointment
                        </Typography>
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="body2"
                            component="h6"
                        >
                            Appointment ID : {sessionId}
                        </Typography>
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="body2"
                            component="h6"
                        >
                            Client ID : {clientId}
                        </Typography>
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="body2"
                            component="h6"
                        >
                            Center : {center}
                        </Typography>
                        <Box sx={{ minWidth: 500, mb: 2 }}>
                            {data && (
                                <Grid container spacing={3}>
                                    <Grid item lg={12} md={6} sm={12} xs={12} sx={{ mt: 2 }}>
                                        <TextField
                                            fullWidth
                                            label="Scheduled At"
                                            inputProps={{ readOnly: true }}
                                            value={data.scheduledAt}
                                        />
                                    </Grid>
                                    <Grid item lg={12} md={6} sm={12} xs={12}>
                                        <TextField
                                            fullWidth
                                            label="Reason"
                                            multiline
                                            rows={4}
                                            // inputProps={{ readOnly: true }}
                                            // defaultValue={data.scheduledAt}
                                            inputProps={{ readOnly: data.disabled }}
                                            value={rejectReason}
                                            onChange={(e) => setRejectReason(e.target.value)}
                                            validators={['REQUIRED']}
                                            errorMessages={['This field is required']}
                                        />
                                        
                                    </Grid>
                                    {error && (
                                            <span style={{ marginLeft:20, color: 'red' }}>
                                                Error: Please add the reason of cancellation
                                            </span>
                                        )}
                                </Grid>
                            )}
                        </Box>

                        <Box
                            sx={{
                                display: 'table',
                                marginLeft: 'auto',
                                marginRight: '0px',
                            }}
                        >
                            <Button
                                color="error"
                                variant="contained"
                                onClick={() => {
                                    // dispatch(resetClient())
                                    closeModal()
                                }}
                            >
                                Cancel
                            </Button>
                            <Button
                                disabled={props.loading}
                                sx={{ ml: 2 }}
                                type="submit"
                                variant="contained"
                            >
                                Reject
                            </Button>
                            {props.loading && (
                                <StyledProgress
                                    size={24}
                                    className="buttonProgress"
                                />
                            )}
                        </Box>
                    </ValidatorForm>
                </Box>
            </Modal>
        </div>
    )
}

export default RejectModal