import React, { useEffect, useState } from 'react'
import {
    Box,
    Button,
    Modal,
    Typography,
    CircularProgress,
    Grid,
    TextField,
} from '@mui/material'

import { styled } from '@mui/system'
import { ValidatorForm } from 'react-material-ui-form-validator'
const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    bottom: '38px',
    right: '60px',
}))

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    // width: '100%',
    bgcolor: 'background.paper',
    border: '2px solid #fff',
    boxShadow: 24,
    p: 4,
    borderRadius: '15px',
}

const AcceptModal = (props) => {
    const [sessionId, setSessionId] = useState('')
    const [clientId, setClientId] = useState('')
    const [center, setCenter] = useState('')
    const [data, setData] = useState()

    // const closeModal = () => {
    //     props.handleClose()
    // }

    function handleSubmit(e, data) {
        props.handleSubmit(e, data)
    }

    useEffect(() => {
        ValidatorForm.addValidationRule('REQUIRED', (value) => {
            if (value.length === 0) {
                return false
            }
            return true
        })
        if (props.data) {
            console.log(props.data)
            setSessionId(props.data.sessionId)
            setCenter(props.data.center)
            setClientId(props.data.clientId)
            setData(props.data)
        }
        return () => {
            ValidatorForm.removeValidationRule('REQUIRED')
        }
    }, [props])
    return (
        <div>
            <Modal
                keepMounted
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    <ValidatorForm
                        // ref={formEl}
                        onSubmit={(e) => handleSubmit(e, props)}
                        onError={() => null}
                    >
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="h6"
                            component="h2"
                        >
                            Confirm this appointment
                        </Typography>
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="body2"
                            component="h6"
                        >
                            Appointment ID : {sessionId}
                        </Typography>
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="body2"
                            component="h6"
                        >
                            Client ID : {clientId}
                        </Typography>
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="body2"
                            component="h6"
                        >
                            Center : {center}
                        </Typography>
                        <Box sx={{ minWidth: 500, mb: 2 }}>
                            {data && (
                                <Grid container spacing={3}>
                                    <Grid
                                        item
                                        lg={12}
                                        md={6}
                                        sm={12}
                                        xs={12}
                                        sx={{ mt: 2 }}
                                    >
                                        <TextField
                                            fullWidth
                                            label="Scheduled At"
                                            inputProps={{ readOnly: true }}
                                            value={data.scheduledAt}
                                        />
                                    </Grid>
                                </Grid>
                            )}
                        </Box>

                        <Box
                            sx={{
                                display: 'table',
                                marginLeft: 'auto',
                                marginRight: '0px',
                            }}
                        >
                            <Button
                                color="error"
                                variant="contained"
                                onClick={() => {
                                    // dispatch(resetClient())
                                    props.handleClose()
                                }}
                            >
                                Cancel
                            </Button>
                            <Button
                                disabled={props.loading}
                                sx={{ ml: 2 }}
                                type="submit"
                                variant="contained"
                            >
                                Accept
                            </Button>
                            {props.loading && (
                                <StyledProgress
                                    size={24}
                                    className="buttonProgress"
                                />
                            )}
                        </Box>
                    </ValidatorForm>
                </Box>
            </Modal>
        </div>
    )
}

export default AcceptModal
